div#avatar-container {
  position: relative;
  width: 128px;
  height: 320px;
  padding-top: 80px;
  grid-area: avatar;
}
div#avatar-container .avatar-shadow {
  box-shadow: var(--character-shadow);
  background-color: rgb(0 0 0 / 20%);
  width: 60%;
  height: 23px;
  border-radius: 100%;
  margin: -14px auto 0;
}

div#avatar-container #avatar {
  width: 140px;
}

div#cats {
  position: absolute;
  bottom: 10px;
  right: -140px;
  height: 120px;
  transform: rotateY(180deg);
  transition: all 2s ease;
}

div#avatar-container #cats img {
  position: absolute;
}
#ralph {
  width: 120px;
  transform: translateX(600px);
  z-index: 0;
}
#atreyu {
  width: 100px;
  transform: translateX(800px);
  z-index: 1;
  top: 34px;
}

div#avatar-container #player-controls {
  position: absolute;
  bottom: 5px;
  left: 100%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 2;
  transition: opacity 0.3s ease;
}

div#avatar-container #player-controls.playing {
  animation: pulse 1s ease-in-out infinite;
}

div#avatar-container #player-controls.paused {
  opacity: 0.7;
}

div#avatar-container #player-controls img {
  width: 60px;
  height: auto;
  display: block;
}

@keyframes pulse {
  0% {
    transform: translateX(-50%) scale(1);
  }
  50% {
    transform: translateX(-50%) scale(1.05);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}

@media screen and (max-width: 640px) {
  div#avatar-container #avatar {
    width: 90%;
  }
  #ralph {
    width: 100px;
  }
  #atreyu {
    width: 80px;
  }
  div#avatar-container #player-controls img {
    width: 50px;
  }
}
